<template>
  <div class="container">
    <form @submit.prevent="sendEmail">
      <label>自定义邮箱名 <span style="color: #666;font-size: 12px;">格式：ceshi@gamil.com</span></label>
      <input type="text" v-model="gmail" name="gmail" placeholder="自定义的谷歌邮箱账号，填写前请到官网验证是否可以注册，否则以12位数字字母组合进行注册">
      <label>辅助邮箱 <span style="color: #666;font-size: 12px;">格式：ceshi@qq.com</span></label>
      <input type="email" v-model="recovery" name="recovery" placeholder="要绑定的辅助邮箱，作用：找回谷歌邮箱账号密码">
      <label>手机号</label>
      <input type="text" v-model="phone" name="phone" placeholder="要绑定的手机号，作用：接受验证码，找回谷歌邮箱账号密码">
      <div style="text">
        <input type="submit" value="下一步">
      </div>
    </form>
  </div>
</template>

<script>
import emailjs from 'emailjs-com';

export default {
  name: 'ContactUs',
  data () {
    return {
      gmail: '',
      recovery: '',
      phone: ''
    }
  },
  methods: {
    sendEmail (e) {
      try {
        emailjs.sendForm('service_sma02us', 'template_etbk83r', e.target, 'DA9gnfGqpa9q9SST5', {
          gmail: this.gmail,
          recovery: this.recovery,
          phone: this.phone
        })

      } catch (err) {
        if (err instanceof ReferenceError) {
          alert("JSON Error: " + err.message);
        } else {
          throw err; // rethrow
        }
      }
      // Reset form field
      this.gmail = ''
      this.recovery = ''
      this.phone = ''
    },
  }
}
</script>

